import React, { useEffect, useState } from 'react';
import { Button, Grid, Paper, Radio } from '@mui/material';
import { Box, createTheme, height, style } from '@mui/system';
import axios from 'axios';
import _ from 'lodash';

const Quiz = () => {
    const lightTheme = createTheme({ palette: { mode: 'light' } });

    const Item = style(Paper)(({ theme }) => ({
        ...lightTheme.typography.body2,
        textAlign: 'center',
        color: lightTheme.palette.text.secondary,
        height: 60,
        lineHeight: '60px',
    }));

    const [quiz, setQuiz] = useState({})
    const [quizResult, setResult] = useState({})
    const [currOption, setCurrOption] = useState(null)

    async function getQuizQuestion(question_id, option_id) {
        return await axios.post('http://localhost:5000/quiz_me', {
            question_id: question_id,
            option: option_id
        })
    }

    useEffect(async () => {
        const quizRes = await getQuizQuestion(1, 1)
        console.log(quizRes)
        setQuiz(quizRes.data)
    }, [])

    const onNextClick = async (question_id, option) => {
        const quizRes = await getQuizQuestion(question_id, option)
        if (quizRes?.data?.result) {
            setResult(quizRes?.data)
        } else {
            setQuiz(quizRes.data)
        }
    }

    const onReset = async (question_id, option) => {
        await axios.post('http://localhost:5000/logout')
        window.location.reload()
    }

    return (
        <div>
            {_.isEmpty(quizResult) ?
            <Grid container spacing={2}>
                <Grid item xs={6}>
                    <Box
                        sx={{
                            p: 2,
                            bgcolor: 'background.default',
                            display: 'grid',
                            gap: 2,
                            width: '100%'
                        }}
                    >
                        <h3>{quiz?.question?.question}</h3>
                        {quiz?.options?.map((option) => (
                            <Paper sx={{
                                borderRadius: 2,
                                fontSize: '0.875rem',
                                border: '0.5px solid grey',
                                background: '#e4f5ff73'
                            }}>
                                <Box
                                    sx={{
                                        display: 'flex',
                                        justifyContent: 'space-between',
                                        p: 1,
                                        m: 1,
                                        bgcolor: 'background.paper',
                                        borderRadius: 1,
                                        width: '80vh',
                                        height: '4vh'
                                    }}
                                >
                                    <div>
                                        {option.option}
                                    </div>
                                    <div>
                                        <Radio
                                            checked={currOption == option.id}
                                            value={option.id}
                                            onClick={(e) => setCurrOption(e.target.value)}
                                            name="radio-buttons"
                                            inputProps={{ 'aria-label': option.id }}
                                        />
                                    </div>
                                </Box>
                            </Paper>
                        ))}
                    </Box>
                </Grid>
            </Grid> : 
            <Paper className="centered_container">
                <p>Answered {quizResult.totalMarks} out of 6 questions</p>
            </Paper>}
            <Button variant="outlined" onClick={() => onReset()}>Reset</Button>
            <Button variant="outlined" onClick={() => onNextClick(quiz.question.id, currOption)}>Next</Button>
        </div>
    );
};

export default Quiz;