import React from 'react';
import Quiz from './Quiz';
import { BrowserRouter as Router, Route, Routes, } from "react-router-dom";
import Login from './Login';

const App = () => {
  return (
    <Router>
      <div>
        <Routes>
          <Route path="/">
            <Route index element={<Login />} />
            <Route path="/quiz" element={<Quiz />} />
          </Route>
        </Routes>
      </div>
    </Router>
  );
};

export default App;