import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import { useFormik } from 'formik';
import * as yup from 'yup';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import { Alert, Paper } from '@mui/material';
import { useNavigate } from 'react-router-dom';


const validationSchema = yup.object({
  email: yup
    .string('Enter your email')
    .email('Enter a valid email')
    .required('Email is required'),
  password: yup
    .string('Enter your password')
    .min(8, 'Password should be of minimum 8 characters length')
    .required('Password is required'),
});

const Login = () => {
const navigate = useNavigate();
const [alert, setAlert] = useState(false)
  const formik = useFormik({
    initialValues: {
      email: '',
      password: '',
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      setAlert(false)
      if(values.email == "admin@gmail.com" && values.password == "admin123"){
        navigate('/quiz')
      }else{
        setAlert(true)
      }
    }
  });

  return (
    <Paper className="centered_container">
      <div>
        <h1 style={{ textAlign: "center" }}>Login to Start Quiz</h1>
        <form onSubmit={formik.handleSubmit}>
          <TextField
            fullWidth
            className="form_item"
            id="email"
            name="email"
            label="Email"
            value={formik.values.email}
            onChange={formik.handleChange}
            error={formik.touched.email && Boolean(formik.errors.email)}
            helperText={formik.touched.email && formik.errors.email}
            variant="outlined"
          />
          <TextField
            fullWidth
            className="form_item"
            name="password"
            label="Password"
            type="password"
            value={formik.values.password}
            onChange={formik.handleChange}
            error={formik.touched.password && Boolean(formik.errors.password)}
            helperText={formik.touched.password && formik.errors.password}
            variant="outlined"
          />
          <Button className="form_item" color="primary" variant="contained" fullWidth type="submit">
            Submit
          </Button>
        </form>
        {alert ? <Alert severity="error">Wrong username or password!</Alert> : null}
      </div>
    </Paper>
  );
};

export default Login;